@use "../mixins/respond-to.scss" as *;

.hero {
    position: relative;
    background-color: #F4F5F6;
    height: fit-content;

    @include respond-to(tablet-l) {
        height: auto;
    }

    &__bg {
        position: relative;
        margin-left: auto;
        height: 100%;
        min-height: 350px;

        img {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;

            @include respond-to(mobile) {
                display: none;
                height: auto;
            }
        }
    }

    .pt-95 {
        padding-top: 95px;
    }

    .pb204 {
        padding-bottom: 204px;
    }

    &__content {
        top: 0;
        padding: 152px 0 0 0;

        &--absolute {
            position: absolute;
        }

        &--static {
            position: static;
        }

        @include respond-to(tablet-l) {
            padding: 100px 0 0 0;
        }

        .w100 {
            max-width: unset;
        }

        .ttcc {
            text-transform: unset;
        }

        .fz64 {
            font-size: 64px;

            @include respond-to(laptop) {
                font-size: 36px;
            }
        }

    }

    &__breadcrumbs {
        font-size: 14px;
        color: #424551;

        &--active {
            color: #9A9CA5
        }

        &-wrap {
            margin-bottom: 50px;

            @include respond-to(tablet-l) {
                margin-bottom: 44px;
            }
        }
    }

    &__title {
        width: 100%;
        max-width: 600px;
        font-size: 72px;
        color: #1E212C;
        margin-bottom: 29px;
        text-transform: uppercase;

        @include respond-to(tablet-l) {
            font-size: 30px;
            margin-bottom: 11px;
        }
    }

    &__desc {
        font-size: 20px;
        color: #787A80;
        width: 100%;
        max-width: 600px;
        line-height: 1.5;
        padding-right: 10px;

        @include respond-to(tablet-l) {
            font-size: 15px;
        }
    }
}